<template>
  <div id="app">
    <div class="card1">
      <el-form ref="form" :model="form" label-width="80px" >
        <el-form-item label="电厂名称">
          <el-input v-model="form.powerPlantName"></el-input>
        </el-form-item>
        <el-form-item label="电厂类型">
          <el-select v-model="powerPlantType" placeholder="请选择">
            <el-option v-for="item in options" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item lable-width="120px" label="发电机组容量" width='120'>
          <el-select v-model="generatorCapacity" placeholder="请选择">
            <el-option v-for="item in options1" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="升压站电压等级">
          <el-select v-model="voltageLevel" placeholder="请选择">
            <el-option v-for="item in options2" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="所在省份">
          <el-select v-model="province" placeholder="请选择">
            <el-option v-for="item in options3" :key="item.value" :label="item.label" :value="item.value">
            </el-option>
          </el-select>
        </el-form-item>
        <el-form-item label="电厂地址">
          <el-input v-model="form.address"></el-input>
        </el-form-item>
        <el-form-item label="电话">
          <el-input v-model="form.phone"></el-input>
        </el-form-item>
        <el-form-item label="邮编">
          <el-input v-model="form.postcode"></el-input>
        </el-form-item>
        <el-button type="primary" @click='dataPush'>保存 </el-button>
      </el-form>
    </div>
  </div>
</template>

<script>
  import axios from 'axios'
  export default {
    name: 'xinjian',
    data() {
      return {
        options: [{
          value: '水电厂'
        }, {
          value: '火电厂'
        }, {
          value: '风电厂'
        }, {
          value: '气电厂'
        }],
        options1: [{
          value: '10L'
        }, {
          value: '20L'
        }, {
          value: '30L'
        }, {
          value: '40L'
        }],
        options2: [{
          value: '特级'
        }, {

          value: '1'
        }, {

          value: '2'
        }, {

          value: '3'
        }],
        options3: [{

          value: '陕西省'
        }, {

          value: '山西省'
        }, {

          value: '山东省'
        }, {

          value: '湖北省'
        }],
        powerPlantType: '',
        generatorCapacity: '',
        voltageLevel: '',
        province: '',
        form: {
          powerPlantName: '',
          address: '',
          phone: '',
          postcode: '',
        },

      }
    },
    methods: {
      dataPush() {
        if (!(this.form.powerPlantName && this.form.address && this.form.phone && this.form.postcode && this.powerPlantType && this.generatorCapacity &&
            this.voltageLevel && this.province)) {
          alert('请检查你输入的数据')
        } else {
          var data = {
            powerPlantName: this.form.powerPlantName,
            powerPlantType: this.powerPlantType,
            generatorCapacity: this.generatorCapacity,
            voltageLevel: this.voltageLevel,
            province: this.province,
            
            address: this.form.address,
            phone: this.form.phone,
            postcode: this.form.postcode,
          }
          localStorage.setItem('data', JSON.stringify(data))
          this.$router.push({
            path: './dianchangguanli',
          })
          axios.post('http://127.0.0.1:8084/power/add', {
           address:this.form.address,
           powerPlantName: this.form.powerPlantName,
            powerPlantType: this.powerPlantType,
            generatorCapacity: this.generatorCapacity,
            voltageLevel: this.voltageLevel,
            province: this.province,
            
            address: this.form.address,
            phone: this.form.phone,
            postcode: this.form.postcode,
            })
            .then(function (response) {
              console.log(response);
              
            })
            .catch(function (error) {
              console.log(error);
            });
        }
      }
    }
  }

</script>

<style lang="" scoped>
  
  .card1 {
    width: 550px;
    padding: 10px;
    margin: 0 auto;
    text-align: center;
    background: white;
  }
 
 

  .card1 .el-input {
    width: 400px;
    height: 30px;
  }

  .card1 .el-select {
    width: 400px;
    height: 25px;
  }


  .card1 .el-button {
    padding: 8px 16px;
  }

  .element.style {
    width: 120px;
  }

</style>



